.card {
  font-family: Roboto, sans-serif;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  display: flex;
  transition: transform 300ms;

  &__imac {
    width: 160px;
    height: 134px;
  }

  &__image {
    display: flex;
    width: 160px;
    height: 134px;
    padding: 0 3px;
  }

  &__title {
    margin-top: 40px;
    color: var(--dark-blue);
    margin-bottom: 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    display: block;
  }

  &:hover &__title {
    color: #34568b;
  }

  &__subtitle {
    color: var(--grey);
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &__quality {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  &__reviews {
    color: var(--dark-blue);
    text-align: center;
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    display: block;
  }

  &__cost {
    justify-content: space-between;
    margin-top: 24px;
    margin-bottom: 16px;
    display: flex;
  }

  &__price {
    color: var(--grey);
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }

  &__sum {
    color: var(--dark-blue);
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__buy {
    text-align: center;
    color: var(--white);
    text-transform: uppercase;
    background-color: var(--light-blue);
    border: 1px solid var(--light-blue);
    border-radius: 5px;
    padding: 12px 70px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 40px;

    &:hover {
      color: var(--light-blue);
      background-color: var(--white);
    }
  }

  &:hover {
    transform: scale(1.2);
  }
}
