.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__imac {
  width: 160px;
  height: 134px;
}

.card__image {
  width: 160px;
  height: 134px;
  padding: 0 3px;
  display: flex;
}

.card__title {
  color: var(--dark-blue);
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.card:hover .card__title {
  color: #34568b;
}

.card__subtitle {
  color: var(--grey);
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__quality {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews {
  color: var(--dark-blue);
  text-align: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__cost {
  justify-content: space-between;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  color: var(--grey);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__sum {
  color: var(--dark-blue);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-align: center;
  color: var(--white);
  text-transform: uppercase;
  background-color: var(--light-blue);
  border: 1px solid var(--light-blue);
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__buy:hover {
  color: var(--light-blue);
  background-color: var(--white);
}

.card:hover {
  transform: scale(1.2);
}

* {
  box-sizing: border-box;
}

body {
  --blue: #00acdc;
  --dark-blue: #060b35;
  --grey: #616070;
  --white: #fff;
  --light-blue: #00acdc;
  margin: 0;
}

body p {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
  position: relative;
}

.header-content {
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-item {
  margin-right: 20px;
  display: inline-block;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

li:last-child {
  margin: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
