.nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &-item {
    display: inline-block;
    margin-right: 20px;
  }
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;

  &:hover {
    color: var(--blue);
  }
}

li:last-child {
  margin: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.is-active {
  color: var(--blue);

  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    right: 0;
    height: 4px;
    border-radius: 2px;
    background-color: var(--blue);
  }
}
